.st-tooltip .st-tooltip__tip {
  position: absolute;
  visibility: hidden;
  opacity: 0;
  background-color: #fff;
  border: 1px solid #ccc;
}

.st-tooltip--visible .st-tooltip__tip {
  visibility: visible;
  opacity: 1;
}

.st-tooltip__tip--top:before {
  position: absolute;
  bottom: 0;
  border: 9px solid transparent;
  border-top: 9px solid #ccc;
  content: "";
}
.st-tooltip__tip--top:after {
  position: absolute;
  bottom: 0;
  border: 8px solid transparent;
  border-top: 8px solid #fff;
  content: "";
}

.st-tooltip__tip--top-start:before {
  left: 10px;
  transform: translate(0, 18px);
}
.st-tooltip__tip--top-start:after {
  left: 10px;
  transform: translate(1px, 16px);
}

.st-tooltip__tip--top-end:before {
  right: 10px;
  transform: translate(0, 18px);
}
.st-tooltip__tip--top-end:after {
  right: 10px;
  transform: translate(-1px, 16px);
}

.st-tooltip__tip--bottom:before {
  position: absolute;
  top: 0;
  border: 9px solid transparent;
  border-bottom: 9px solid #ccc;
  content: "";
}
.st-tooltip__tip--bottom:after {
  position: absolute;
  top: 0;
  border: 8px solid transparent;
  border-bottom: 8px solid #fff;
  content: "";
}

.st-tooltip__tip--bottom-start:before {
  left: 10px;
  transform: translate(0, -18px);
}
.st-tooltip__tip--bottom-start:after {
  left: 10px;
  transform: translate(1px, -16px);
}

.st-tooltip__tip--bottom-end:before {
  right: 10px;
  transform: translate(0, -18px);
}
.st-tooltip__tip--bottom-end:after {
  right: 10px;
  transform: translate(-1px, -16px);
}

.st-tooltip__tip--left:before {
  position: absolute;
  left: 100%;
  border: 9px solid transparent;
  border-left: 9px solid #ccc;
  content: "";
}
.st-tooltip__tip--left:after {
  position: absolute;
  left: 100%;
  border: 8px solid transparent;
  border-left: 8px solid #fff;
  content: "";
}

.st-tooltip__tip--left-start:before {
  top: 10px;
  transform: translate(0, 0);
}
.st-tooltip__tip--left-start:after {
  top: 10px;
  transform: translate(0, 1px);
}

.st-tooltip__tip--left-end:before {
  bottom: 10px;
  transform: translate(0, 0);
}
.st-tooltip__tip--left-end:after {
  bottom: 10px;
  transform: translate(0, -1px);
}

.st-tooltip__tip--right:before {
  position: absolute;
  left: 0;
  border: 9px solid transparent;
  border-right: 9px solid #ccc;
  content: "";
}
.st-tooltip__tip--right:after {
  position: absolute;
  left: 0;
  border: 8px solid transparent;
  border-right: 8px solid #fff;
  content: "";
}

.st-tooltip__tip--right-start:before {
  top: 10px;
  transform: translate(-18px, 0);
}
.st-tooltip__tip--right-start:after {
  top: 10px;
  transform: translate(-16px, 1px);
}

.st-tooltip__tip--right-end:before {
  bottom: 10px;
  transform: translate(-18px, 0);
}
.st-tooltip__tip--right-end:after {
  bottom: 10px;
  transform: translate(-16px, -1px);
}